/**
 * 
 * @param value 
 */
export function numberFormat(value: number) {
  const newValue = ["", "", ""];
  let fr = 1000;
  let num = 3;
  let text1 = "";
  let fm = 1;
  while (value / fr >= 1) {
    fr *= 10;
    num += 1;
  }
  if (num <= 4) {
    // 
    newValue[0] = String(value / 1000);
    newValue[1] = "";
  } else if (num <= 8) {
    // 
    text1 = (num - 4) / 3 > 1 ? "" : "";
    fm = text1 === "" ? 10000 : 10000000;
    if (value % fm === 0) {
      newValue[0] = String(value / fm);
    } else {
      newValue[0] = String((value / fm).toFixed(2));
    }
    newValue[1] = text1;
  } else if (num <= 16) {
    // 
    text1 = (num - 8) / 3 > 1 ? "" : "";
    text1 = (num - 8) / 4 > 1 ? "" : text1;
    text1 = (num - 8) / 7 > 1 ? "" : text1;
    fm = 1;
    if (text1 === "") {
      fm = 100000000;
    } else if (text1 === "") {
      fm = 100000000000;
    } else if (text1 === "") {
      fm = 1000000000000;
    } else if (text1 === "") {
      fm = 1000000000000000;
    }
    if (value % fm === 0) {
      newValue[0] = String(value / fm);
    } else {
      newValue[0] = String((value / fm).toFixed(2));
    }
    newValue[1] = text1;
  }
  if (value < 1000) {
    newValue[0] = String(value);
    newValue[1] = "";
  }
  return newValue.join("");
}
